* {
    box-sizing: border-box;
}

h1,h2,h3,h4,h5,h6,p,div {
    margin: 0;
    padding: 0;
}

.card-container {
    width: 550px;
    min-width: 550px;
    height: 750px;
    min-height: 550px;
    margin: 0 auto;
    border: 1px solid black;
    background-color: #23252C;
}

.inner-container {
    width: 317px;
    min-width: 317px;
    margin: 15px auto;
    border: 1px solid black;
}

.card-image {
    width: 100%;
    height: 317px;
}

.card-image img {
    width: 100%;
    height: 100%;
}

.text-container {
    width: 100%;
    min-width: 100%;
    height: 350px;
    min-height: 350px;
    background-color: #F5F5F5;
    text-align: center;
    padding: 21px 25px;
}

.text-container h1 {
    color: black;
    font-size: 30px;
}

.text-container p {
    color: #b36e77;
    font-size: 20px;
    font-weight: bold;
}

.btn-container {
    width: 100%;
    display: flex;
    margin-top: 15px;
    justify-content: space-evenly;
}

.btn {
    padding: 10px 35px;
    border: 1px solid grey;
}

.about-and-interest-container {
    margin-top: 30px;
}

.about-and-interest {
    text-align: left;
    margin-bottom: 15px;
}

.about-and-interest:last-child {
    text-align: left;
    margin-bottom: 5px;
}

.about-and-interest h2 {
    color: black;
    font-size: 18px;
    margin-bottom: 3px;
    font-weight: bolder;
}

.about-and-interest p {
    color: black;
    font-size: 13px;
    line-height: 18px;
}

.footer {
    width: 100%;
    height: 45px;
    background-color: #D5D4D8;
    display: flex;
    align-items: center;
    justify-content: center;
}
